import React, { useState, useEffect } from "react";
import Modal from "react-modal";
import CopyClipboard from "components/CopyClipboard";

import { Grid } from "@mui/material";
import VuiTypography from "components/VUI/VuiTypography";
import VuiBox from "components/VUI/VuiBox";
import VuiButton from "components/VUI/VuiButton";

import { AiFillCloseCircle } from "react-icons/ai";

import { baseUrl } from "constants/config";
import { useWeb3React } from '@web3-react/core';
import { getTokenURI, getIdOfUser, getUsercode } from "utils/contract";

Modal.setAppElement("#root");

export default function CardView(props) {
  
  const { isOpen, toggleModal } = props;
  const {active, account} = useWeb3React();
  const [nftUri, setNftUri] = useState("");
  const [usercode, setUsercode] = useState("");
  const domain = "https://chad-card1.herokuapp.com/";

  useEffect(async()=>{
    if (active) {
        let code = await getUsercode(account);
        setUsercode(code);
        let id = await getIdOfUser(account);
        let tokenUri = await getTokenURI(id);
        setNftUri(baseUrl+tokenUri);
    }
  }, [active, account]);
  return (
    <div className="card-view">
      <Modal
        isOpen={isOpen}
        onRequestClose={toggleModal}
        contentLabel="My dialog"
        className="mymodal"
        overlayClassName="myoverlay"
        closeTimeoutMS={500}
      >
        <AiFillCloseCircle onClick={toggleModal} className="icon" size={28}/>
        <VuiBox width="99%" mt={5} display="flex" flexDirection="row" justifyContent="center">
            <VuiBox
              width="100%"
              display="flex"
              justifyContent="space-between"
              mt="20px"
              alignItems="center"
            >
              <VuiTypography
                width="calc(100% - 38px)"
                textAlign="center"
                variant="h5"
                fontWeight="medium"
                color="white"
                sx={{ mb: 1, letterSpacing: "0px" }}
              >
                  {domain+usercode}
              </VuiTypography>
                <CopyClipboard text={domain+usercode} />
            </VuiBox>
        </VuiBox>
        <VuiBox mt={2} style={{opacity: 1}}>
            <div style={{display: "flex", justifyContent: "center"}}>
                <div><img style={{width: "100%"}} src={nftUri} alt="No NFT Card Image"/></div>
            </div>
        </VuiBox>
      </Modal>
    </div>
  );
}
