import {useState, useEffect} from "react";
import { Grid } from "@mui/material";
import VuiTypography from "components/VUI/VuiTypography";
import VuiBox from "components/VUI/VuiBox";
import VuiButton from "components/VUI/VuiButton";

import { getTokenURI, getIdOfUser } from "utils/contract";
import { baseUrl } from "constants/config";
import { download } from "utils/downloader";
import { useWeb3React } from '@web3-react/core';
import "./index.css";
import CopyClipboard from "components/CopyClipboard";


const CardInfo = (props)=>{
    const {account} = useWeb3React();
    const [nftUri, setNftUri] = useState("");
    const domain = "https://chad-card1.herokuapp.com/";

    const downloadCard = (nftUri) => {
        if (window.innerWidth > 900) {
            download(nftUri);
        } else {
            alert("Please take a screenshot of your VIP card.")
        }
    }

    useEffect(async()=>{
        let id = await getIdOfUser(account);
        let tokenUri = await getTokenURI(id);
        setNftUri(baseUrl+tokenUri);
    }, []);

    return(
        <>
            <Grid container spacing={3} justifyContent="space-between">
                <Grid container item xs={12} sm={12} md={12} lg={6} spacing={3}  alignItems="center">
                    <Grid item xs={12} sm={12}>
                        
                        <VuiBox style={{opacity: 1}}>
                            <div style={{display: "flex", justifyContent: "center"}}>
                                <div><img style={{height: "180px", width: "242px"}} src={require("assets/img/Chad Inu Logo.png").default} alt="No Logo Image"/></div>
                            </div>
                        </VuiBox>

                        <VuiTypography
                            textAlign="center"
                            variant="h2"
                            fontWeight="medium"
                            color="white"
                            mb="10px"
                            sx={{ mb: 1, letterSpacing: "8px" }}
                        >
                            Congratulations!
                        </VuiTypography>
                        
                        <VuiTypography
                            textAlign="center"
                            variant="h3"
                            fontWeight="bold"
                            color="white"
                            mb="10px"
                            // textGradient
                            sx={{ letterSpacing: "8px" }}
                        >
                            You are a Chad Inu VIP Partner. Save a copy of your VIP Card to your device, and SHARE with fellow Chads to start earning REWARDS! 
                        </VuiTypography>
                        
                        <VuiBox width="100%" display="flex" flexDirection="row" justifyContent="center">
                            <VuiBox
                                display="flex"
                                justifyContent="space-between"
                                alignItems={{ xs: "center", md: "flex-end" }}
                                flexDirection={{ xs: "column", md: "row" }}
                                mt="1.5rem"
                            >
                                <VuiTypography
                                    textAlign="center"
                                    variant="h5"
                                    fontWeight="medium"
                                    color="white"
                                    mr="10px"
                                    sx={{ mb: 1, letterSpacing: "0px" }}
                                >
                                    {domain+props.code}
                                </VuiTypography>
                                <CopyClipboard text={domain+props.code} />
                            </VuiBox>
                        </VuiBox>
                        
                        <VuiBox width="100%" mt={2} display="flex" flexDirection="row" justifyContent="center">
                            <VuiButton 
                                variant="contained" 
                                color="info"
                                onClick={()=>downloadCard(nftUri)}
                            >
                                {window.innerWidth > 900 ? "Download Your Card" : "Screenshot Your Card"}
                            </VuiButton>
                            <VuiButton 
                                style={{marginLeft: "10px"}}
                                variant="contained" 
                                color="info"
                                onClick={()=>downloadCard(nftUri)}
                            >
                                Visit Your VIP Dashboard
                            </VuiButton>
                        </VuiBox>                    
                    </Grid>
                </Grid>

                <Grid container item xs={12} sm={12} md={12} lg={6} spacing={3} alignItems="center">
                    <Grid item xs={12}>
                        <VuiBox mt={5} style={{opacity: 1}}>
                            <div style={{display: "flex", justifyContent: "center", height: "637px"}}>
                                <div><img className="card-img" src={nftUri} alt="No NFT Card Image"/></div>
                            </div>
                        </VuiBox>
                    </Grid>
                </Grid>
            </Grid>
        </>
    );
}

export default CardInfo;